import React, {useState, useEffect} from 'react'
import ReactEcharts from "echarts-for-react"
import {Decoration1} from '@jiaminghi/data-view-react'

import './RoseChart.css'

export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}

	render() {
		const {title, option, onEvents} = this.props;

		return (
			<div id="rose-chart2" className={title.includes("人员") ? "charts-role":""} style={{width: title.includes("项目") || title.includes("人员") ? '22%' : '29%'}}>
				<div className="rose-chart-title">
					<p>{title}</p>
					<Decoration1
						className="rose-chart-deco"
						style={{width: '110px', height: '20px'}}/>
				</div>
				<ReactEcharts
					className="dv-charts-container"
					onEvents={onEvents}
					height={'100%'}
					option={option}
					notMerge={true}
					lazyUpdate={true}
				/>
			</div>
		)
	}
}



