import React, {useState, useEffect} from 'react'
import './RoseChart.css'
import ReactEcharts from "echarts-for-react";
import echarts from "echarts";
import * as mobx from "mobx";
import {Decoration1} from "@jiaminghi/data-view-react";

function rmoney(e) {
	return parseFloat(e.replace(/[^\d\.-]/g, ""));
}

export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}

	render() {
		const {yysrChartData, title, type, onEvents} = this.props;
		const option = {
			color: ['rgb(255,153,106)', 'rgb(117, 232, 242)', 'rgb(255, 184, 0)'],
			tooltip: {},
			grid: {
				top: '50px',
				left: '10px',
				right: '10px',
				bottom: '10px',
				containLabel: true
			},
			xAxis: [
				{
					type: 'category',
					data: mobx.toJS(yysrChartData).category && mobx.toJS(yysrChartData).category,
					axisTick: {
						alignWithLabel: true
					},
					axisLine: {
						lineStyle: {
							color: "#fff",
						}
					},
					/*axisLabel: {
						interval: 0,//横轴信息全部显示
						rotate: 60,//60度角倾斜显示
						// formatter: function (val) {
						// 	return val.split("").join("\n");
						// } //横轴信息文字竖直显示
					}*/
		        }
			],
			yAxis: [
				{
					type: 'value',
					scale: true,
					axisLine: {
						show: false,
						lineStyle: {
							color: "#fff",
						}
					},
					axisTick: {
						show: false
					},
					splitLine: {
						show: true,
						lineStyle: {
							color: ['#46596B'],
							width: 1,
							type: 'solid'
						}
					}
				}
			],
			series: mobx.toJS(yysrChartData).series && mobx.toJS(yysrChartData).series.map((item, index) => {
				if (type == "ydsj") {  // 营业收入
					return {
						name: item.name,
						type: 'line',
						data: item.value.map((vitem) => {
							return rmoney(vitem)
						}),
						smooth: true,
						sampling: 'average',
						showAllSymbol:true,
						areaStyle: {
							normal: {
								//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
								color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
									offset: 0,
									color: 'rgb(255,153,106, 0.89)'
								}, {
									offset: .34,
									color: 'rgb(255,153,106, 0.45)'
								}, {
									offset: 1,
									color: 'rgb(255,153,106, 0.00)'
								}])
							}
						},//区域颜色渐变
					}
				}
			})
		};

		return (
			<div id="rose-chart2" style={{width: '24%'}}>
				<div className="rose-chart-title">
					<p>{title}</p>
					<Decoration1
						className="rose-chart-deco"
						style={{width: '110px', height: '20px'}}/>
				</div>
				<ReactEcharts
					className="dv-charts-container"
					onEvents={onEvents}
					option={option}
					notMerge={true}
					lazyUpdate={true}
				/>
			</div>
		)
	}
	}



