import React, {useState, useEffect} from 'react'
import {DigitalFlop, Decoration10, Decoration1} from '@jiaminghi/data-view-react'
import './DigitalFlop.css'
import * as mobx from "mobx";

export default class DigitalFlop111 extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}

	render() {
		const digitalFlopData = [
			{
				title: '营业收入',
				number: {
					number: [mobx.toJS(this.props.data1).length > 0 && mobx.toJS(this.props.data1)[0].yysr],
					content: '{nt}',
					textAlign: 'right',
					style: {
						fill: '#4d99fc',
						fontWeight: 'bold',
					},
				},
				unit: '万',
			},
			{
				title: '营业成本',
				number: {
					number: [mobx.toJS(this.props.data1).length > 0 && mobx.toJS(this.props.data1)[0].yycb],
					content: '{nt}',
					textAlign: 'right',
					style: {
						fill: '#f46827',
						fontWeight: 'bold',
					},
				},
				unit: '万',
			},
			{
				title: '毛利',
				number: {
					number: [mobx.toJS(this.props.data1).length > 0 && mobx.toJS(this.props.data1)[0].ml],
					content: '{nt}',
					textAlign: 'right',
					style: {
						fill: '#40faee',
						fontWeight: 'bold',
					},
				},
				unit: '万',
			},
			{
				title: '费用',
				number: {
					number: [mobx.toJS(this.props.data1).length > 0 && mobx.toJS(this.props.data1)[0].fy],
					content: '{nt}',
					textAlign: 'right',
					style: {
						fill: '#4d99fc',
						fontWeight: 'bold',
					},
				},
				unit: '万',
			},
			{
				title: '净利润',
				number: {
					number: [mobx.toJS(this.props.data1).length > 0 && mobx.toJS(this.props.data1)[0].jlr],
					content: '{nt}',
					textAlign: 'right',
					style: {
						fill: '#f46827',
						fontWeight: 'bold',
					},
				},
				unit: '万',
			},

		]

		return (
			<div id="digital-flop2">
				{/*<div className="rose-chart-title">
					财务情况总览
					<Decoration1 style={{width: '90px', height: '20px'}} />
				</div>*/}
				{digitalFlopData.map(item => (
					<div className="digital-flop-item" key={item.title}>
						<div className="digital-flop-title">{item.title}</div>
						<div className="digital-flop">
							<DigitalFlop config={item.number} style={{width: '80%', height: '50px'}}/><p style={{display: 'flex',
							alignItems: 'center'}}>万</p>
							{/*<div className="unit">{item.unit}</div>*/}
						</div>
					</div>
				))}
				<Decoration10/>
			</div>
		)
	}
}



