import React from 'react';
import ReactDOM from 'react-dom';
import {Link} from 'react-router-dom';
import {Row, Col, Empty, Typography, Card} from 'antd';
import {inject, observer} from 'mobx-react';
import * as  mobx from 'mobx';
import moment from 'moment';
import url from 'url';
//import BigScreen from '@/components/kanban/BigScreen';
import BigScreenThree  from '@/components/datav2/index';


const {Paragraph} = Typography;

@inject('kanbanStore')
@observer
export default class Home extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			key: '', // 判断key值
			iFrameHeight: '100%',//'0px',  // iframe高度
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		let search = nextProps.location.search;
		const ke = url.parse(search, true).query.key;   // 0 经营驾驶 1 财务 2 营销 3 项目 4 人力
		let key = ke == undefined ? nextProps.kanbanStore.navKey : ke;   // 切换经营看板时
		this.setState({
			key
		});
	}

	componentDidMount() {
		let search = this.props.location.search;
		const ke = url.parse(search, true).query.key;   // 0 经营驾驶 1 财务 2 营销 3 项目 4 人力
		let key = ke == undefined ? this.props.kanbanStore.navKey : ke;   // 切换经营看板时
		this.setState({
			key
		});

		// 1、阻止浏览器刷新重置
		if (localStorage.getItem('navKey') == null||localStorage.getItem('navKey')==undefined) {
			// 获取导航菜单 浏览器刷新重置
			this.props.kanbanStore.getNav({
				level: 1,
				parentId: ""
			}).then((res) => {
				if (res&&res.length>0) {
					this.props.kanbanStore.navsData = Object.assign([], res);
					this.props.kanbanStore.navKey = res[0].id;   // 当前活动的取第一个数据
					this.props.kanbanStore.defaultKey = res[0].id;   // 默认取第一个数据
					localStorage.setItem('navKey', res[0].id)
				}
			});
		} else {
			// 2、获取导航菜单 浏览器刷新重置
			this.props.kanbanStore.getNav({
				level: 1,
				parentId: ""
			}).then((res) => {
				if (res&&res.length>0) {
					this.props.kanbanStore.navsData = Object.assign([], res);
				}
			});
		}

	}

	componentWillMount() {
		localStorage.setItem('navKey', null)
	}

	render() {
		const {listData, data1, monthData, monthChartData, yysrChartData, ydfyChartData, ydsjChartData,ydxmChartData,ydxmData,ydryChartData} = this.props.kanbanStore;
		const {key} = this.state;
		let data = (
			<Row>
				{key=="1" ? mobx.toJS(listData).length>0 && mobx.toJS(listData).map((vitem, vindex) => {
					return (<Col key={"list_" + vindex}
						     xs={24} sm={12} md={12} lg={12} xl={6}>
							{vitem.data&&vitem.data.length>0?<Card bordered={false} className="mt20 mr20 kanban_card" style={{maxWidth: '300px'}}>
								<h3 className="kanban_title mb20">{vitem.name}</h3>
								{vitem.data.length>0&&vitem.data.map((item, index) => {
									let month;
									 if (Array.isArray(item.month) && item.month.length>0) {
										 month = item.month[0].slice(-2);
									 } else {
										 // 处理moment月份 0 - 11
										 const month2=moment().month()+1;
										 if(month2<=9) {
											 month="0"+month2
										 } else {
											 month=month2
										 }
									 }

										return (
											<div className="kanban_item item_1">
												<Link
													to={`/kanban/` + item.code + `?type=home&id=${item.id}&time=${item.lastUpdateTime}&key=${item.parentId}` + '&month='+month}>
													<div className="img tfc">
														<img src={item.ico}/>
													</div>
													<p className="p1">{item.name}</p>
													<p className="p2">
														<Paragraph ellipsis>
															更新时间 {moment(item.lastUpdateTime).format("YYYY-MM-DD HH:mm:ss")}
														</Paragraph>
													</p>
												</Link>
											</div>)
									})
								}
							</Card>:null}
						</Col>)
				}) :
					mobx.toJS(listData).length>0 && mobx.toJS(listData).map((item, index) => {
						let month;
						if (Array.isArray(item.month) && item.month.length>0) {
							month = item.month[0].slice(-2);
						} else {
							// 处理moment月份 0 - 11
							const month3=moment().month()+1;
							if(month3<=9) {
								month="0"+month3
							} else {
								month=month3
							}
						}

						return (
							<Col key={"list_" + index}
							     xs={24} sm={24} md={12} lg={10} xl={8}>
								<div className="kanban_item item_1">
									<Link to={`/kanban/`+item.code+`?type=home&id=${item.id}&time=${item.lastUpdateTime}&key=${item.parentId}`+ '&month='+month}>
										<div className="img tfc">
											<img src={item.ico}/>
										</div>
										<p className="p1">{item.name}</p>
										<p className="p2">
											<Paragraph ellipsis>
												更新时间 {moment(item.lastUpdateTime).format("YYYY-MM-DD HH:mm:ss")}
											</Paragraph>
										</p>
									</Link>
								</div>
							</Col>
						)
					})
				}
			</Row>
		)
		return (
			<div className="h" style={{marginLeft: key=="0"?"-20px": "0"}}>
				{/*自适应大屏*/}
				{key=="0"?
					<BigScreenThree />
					:data}
			</div>
		);
	}
}







