import React from 'react'
import {Decoration3, ScrollRankingBoard} from '@jiaminghi/data-view-react'
import './RankingBoard.css'

export default class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    const {title, config} = this.props;

    return (
        <div id="ranking-board2">
          <div className="ranking-board-title">
              {title}
              <Decoration3
                  className="rose-chart-deco"
                  style={{width: '90px', height: '15px'}} />
          </div>
          <ScrollRankingBoard config={config} />
        </div>
    )
  }
}




