import React from 'react'

import {WaterLevelPond, DigitalFlop} from '@jiaminghi/data-view-react'
import './WaterLevelChart.css'
import * as mobx from "mobx";

export default (props) => {
	const totalYearTarget = mobx.toJS(props.data).length > 0 && mobx.toJS(props.data)[0].totalYearTarget;
	const totalYearActual = mobx.toJS(props.data).length > 0 && mobx.toJS(props.data)[0].totalYearActual;
	const yearRate = mobx.toJS(props.data).length > 0 && mobx.toJS(props.data)[0].yearRate;
	const config = {
		data: [parseFloat(yearRate) || 0],
		shape: 'round',
		waveHeight: 25,
		waveNum: 2,
	}


	return (
		<div id="water-level-chart2">
			<div className="water-level-chart-title">累计完成率</div>
			<div className="chart-container">
				<WaterLevelPond
					config={config}/>
			</div>
			<div className="card-footer">
				<div className="card-footer-item">
					<div className="footer-title" title="年度目标">年度目标</div>
					<div className="footer-detail" title={totalYearTarget}>
						<DigitalFlop
							config={{
								number: [totalYearTarget],
								content: '{nt}',
								textAlign: 'left',
								style: {
									fill: '#26fcd8',
									fontWeight: 'bold',
									fontSize: '12'
								}
							}}
							style={{width: '100%', height: '35px'}}/><span style={{fontSize: '12px'}}>万</span>
					</div>
				</div>
				<div className="card-footer-item">
					<div className="footer-title" title="实际完成">实际完成</div>
					<div className="footer-detail" title={totalYearActual}>
						<DigitalFlop config={{
							number: [totalYearActual],
							content: '{nt}',
							textAlign: 'left',
							style: {
								fill: '#26fcd8',
								fontWeight: 'bold',
								fontSize: '12'
							}
						}} style={{width: '100%', height: '35px'}}/><span style={{fontSize: '12px'}}>万</span>
					</div>
				</div>
			</div>
		</div>
	)
}



