	// 全屏切换
	handleCheck = () => {
		let url = window.location.origin + window.location.pathname + "#/bigScreenTwo"
		var win = window.open(url, '_blank');
		win.focus();
	}

	// 查询权限
	queryCharts = (classifyId, type) => {
		const param = {
			classifyId: classifyId,
			type: 0
		}
		this.props.kanbanStore.getPermissionByClassifyId(param).then((res) => {
			this.setState({
				[type]: res
			})
		});
	}

	// 点击柱
	onChartClick = (type, e) => {
		let _this = this;
		const {name} = e;
		const data = {
			'1月': '01',
			'2月': '02',
			'3月': '03',
			'4月': '04',
			'5月': '05',
			'6月': '06',
			'7月': '07',
			'8月': '08',
			'9月': '09',
			'10月': '10',
			'11月': '11',
			'12月': '12'
		}
		if (type == '1') {
			// 统一处理日期
			//const month = name && name.slice(-2);
			const month = data[name];
			// 合同
			_this.props.history.push('/kanban/htqk?type=home&key=1&id=12&month=' + month)
		} else if (type == '2') {
			const month = data[name];
			// 商机
			_this.props.history.push('/kanban/sjqk?type=home&key=2&id=21&month=' + month);
		} else if (type == '3') {
			const month = data[name];
			// 营业收入
			_this.props.history.push('/kanban/yysr?type=home&key=1&id=14&month=' + month)
		} else if (type == '4') {
			const month = data[name];
			// 项目情况
			_this.props.history.push('/kanban/xmqk?type=home&key=3&id=31&month=' + month)
		} else if (type == '5') {
			const month = data[name];
			// 人员
			_this.props.history.push('/kanban/ryqk?type=home&key=1&id=41&month=' + month)
		} else if (type == '6') {
			const {seriesName} = e;
			const month = data[name];
			const {rlState, jcState, zxState} = this.state;
			if (seriesName == "人力成本") {
				if (rlState) {
					_this.props.history.push('/kanban/rlcb?type=home&key=1&id=13&month=' + month)
				} else {
					return false;
				}
			} else if (seriesName == "专项费用") {
				if (zxState) {
					_this.props.history.push('/kanban/zxfy?type=home&key=1&id=15&month=' + month)
				} else {
					return false;
				}
			} else if (seriesName == "基础费用") {
				if (jcState) {
					_this.props.history.push('/kanban/jcfy?type=home&key=1&id=16&month=' + month)
				} else {
					return false;
				}
			}
		}
	}

