import React from 'react'
import './RankingBoard.css'
import ReactEcharts from "echarts-for-react";
import {Decoration3, Charts} from "@jiaminghi/data-view-react";

export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.state = {}
	}

	componentDidMount() {

	}

	componentWillUnmount() {

	}

	render() {
		const {title, option, length} = this.props;

		return (
			<div id="ranking-board2" style={{width: '20%'}}>
				<div className="ranking-board-title">
					{title}
					<Decoration3
						className="rose-chart-deco"
						style={{width: '90px', height: '15px'}}/>
				</div>
				{
					length>0&&<ReactEcharts
						className="dv-charts-container"
						option={option}
						notMerge={true}
						lazyUpdate={true}
					/>
				}
			</div>
		)
	}
}




